.container {
  max-width: 1170px;
  margin: 0 30px;
  position: relative;
}

@media (min-width: 640px) {
  .container {
    margin: 0 34px;
  }
}

@media (min-width: 1280px) {
  .container {
    margin: 0 auto;
  }
}

.section {
  margin-bottom: 110px;
}

@media (min-width: 640px) {
  .section {
    margin-bottom: 135px;
  }
}

@media (min-width: 1280px) {
  .section {
    margin-bottom: 150px;
  }
}

.page__main {
  overflow-x: hidden;
}

.page__content--with-menu {
  overflow-y: hidden;
}

.header {
  margin-bottom: 110px;
}

@media (min-width: 640px) {
  .header {
    margin-bottom: 135px;
  }

  .header:before {
    content: "";
    object-fit: cover;
    background-image: url("header_bg.fb8694a2.png");
    background-repeat: no-repeat;
    width: 80%;
    height: 100%;
    position: absolute;
    top: -12%;
    left: 0;
  }
}

@media (min-width: 1280px) {
  .header {
    margin-bottom: 150px;
  }

  .header:before {
    top: 0;
  }
}

.header__container {
  max-width: 1170px;
  margin: 0 auto;
}

.header__top {
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  margin: 15px 30px 10px;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

@media (min-width: 640px) {
  .header__top {
    margin: 30px 34px;
  }
}

@media (min-width: 1280px) {
  .header__top {
    margin: 20px 0;
  }
}

.header__nav {
  display: none;
}

@media (min-width: 1280px) {
  .header__nav {
    display: block;
  }
}

.header__list {
  -ms-flex-align: center;
  align-items: center;
  gap: 60px;
  display: -ms-flexbox;
  display: flex;
}

.header__link {
  color: #4f4f4f;
  display: block;
}

.header__link:after {
  content: "";
  background-color: #4f4f4f;
  width: 0;
  height: 1px;
  margin-top: 6px;
  transition: width .3s;
  display: block;
}

.header__link:hover:after {
  width: 100%;
}

.header__cart {
  display: none;
}

@media (min-width: 1280px) {
  .header__cart {
    background-image: url("cart-bag.a11b3f2e.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 30px;
    height: 30px;
    display: block;
  }
}

.header__icon {
  background-image: url("menu_icon.a577a542.svg");
  background-size: cover;
  width: 32px;
  height: 32px;
}

@media (min-width: 1280px) {
  .header__icon {
    display: none;
  }
}

.header__main {
  position: relative;
}

@media (min-width: 640px) {
  .header__main {
    grid-column-gap: 20px;
    grid-template-columns: repeat(6, 1fr);
    margin: 0 34px;
    padding-top: 20px;
    display: grid;
  }
}

@media (min-width: 1280px) {
  .header__main {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    margin: 0;
    padding-top: 56px;
  }
}

.header__picture {
  background-image: url("header_pot_smal.bd34b549.png");
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 320px;
}

@media (min-width: 640px) {
  .header__picture {
    -ms-flex-order: 1;
    order: 1;
    grid-area: 1 / 4 / auto / -1;
    justify-self: end;
    width: 306px;
    height: 306px;
    transition: border-radius .3s;
  }

  .header__picture:hover {
    border-radius: 10%;
  }
}

@media (min-width: 1280px) {
  .header__picture {
    grid-column: 7 / -1;
    width: 500px;
    height: 500px;
    margin: 0;
  }
}

.header__title {
  color: #03839e;
  margin: 0 30px 30px;
}

@media (min-width: 640px) {
  .header__title {
    letter-spacing: 9.92px;
    text-transform: uppercase;
    z-index: 1;
    grid-area: 1 / 1 / auto / 5;
    width: 460px;
    margin: 0;
    padding-bottom: 15px;
    font-family: Bebas Neue, sans-serif;
    font-size: 64px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
  }
}

@media (min-width: 1280px) {
  .header__title {
    grid-column: 2 / 5;
  }
}

.header__title span {
  color: rgba(0, 0, 0, 0);
  -webkit-text-stroke: 1px #03839e;
}

.header__price-wrapper {
  margin: 0 30px 20px;
}

@media (min-width: 640px) {
  .header__price-wrapper {
    z-index: 1;
    grid-area: 1 / 3 / auto / 5;
    -ms-flex-item-align: end;
    align-self: flex-end;
    margin: 0;
  }
}

@media (min-width: 1280px) {
  .header__price-wrapper {
    grid-column: 5 / 7;
    transform: translateY(-142px);
  }
}

.header__price {
  color: #333;
  font-family: Manrope-Bold, sans-serif;
  font-size: 26px;
  font-weight: 700;
  line-height: 150%;
}

.header__text {
  color: #4f4f4f;
  max-width: 140px;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  line-height: 150%;
}

.header__button {
  z-index: 2;
  width: 260px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 640px) {
  .header__button {
    grid-column: 2 / 5;
    margin-top: 50px;
    position: static;
    transform: translateX(0);
  }
}

@media (min-width: 1280px) {
  .header__button {
    grid-area: 1 / 4 / auto / 6;
    -ms-flex-item-align: end;
    align-self: end;
    margin-bottom: 42px;
  }
}

.header__about {
  writing-mode: vertical-lr;
  color: #03839e;
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 2%;
}

.header__about:after {
  content: "";
  background-image: url("arrow_blue_down.7a2295ab.svg");
  background-repeat: no-repeat;
  width: 10px;
  height: 46px;
  margin-top: 10px;
  transition: margin .3s;
  display: block;
}

.header__about:hover:after {
  margin-top: 20px;
  margin-bottom: -10px;
}

@media (min-width: 640px) {
  .header__about {
    bottom: -18%;
    left: 5px;
    right: 100%;
  }
}

@media (min-width: 1280px) {
  .header__about {
    bottom: 0;
  }
}

.logo {
  transition: scale .3s;
}

.logo:hover {
  scale: 1.2;
}

