.button {
  text-transform: uppercase;
  cursor: pointer;
  border: none;
  outline: none;
  width: 238px;
  padding: 14px 0;
  font-family: Manrope-SemiBold, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
}

.button:after {
  content: "";
  background-image: url("arrow_right.9d921c8f.svg");
  background-repeat: no-repeat;
  width: 27px;
  height: 10px;
  transition: left .3s;
  display: inline-block;
  position: relative;
  top: 1px;
  left: 10px;
}

.button:hover:after {
  left: 20px;
}

.button__primary {
  color: #fff;
  background-color: #03839e;
  transition: background-color .3s;
}

.button__primary:after {
  background-image: url("arrow_white.2a5d1081.svg");
}

.button__primary:hover {
  background-color: #2b93ab;
}

.button__primary2 {
  background-color: #ffe339;
  transition: background-color .3s;
}

.button__primary2:hover {
  background-color: #ffec7a;
}

.button__secondary {
  background-color: inherit;
  border: 1px solid #03839e;
  transition: all .3s;
}

.button__secondary:hover {
  color: #fff;
  background-color: #03839e;
}

.button__secondary:hover:after {
  background-image: url("arrow_white.2a5d1081.svg");
}

.button__secondary2 {
  border: 1px solid #333;
  transition: background-color .3s;
}

.button__secondary2:hover {
  background-color: #fff;
}

.menu {
  z-index: 3;
  background-color: #03839e;
  width: 80%;
  height: 100vh;
  transition: transform .3s;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu:target {
  transform: translateX(0);
}

.menu__container {
  -ms-flex-pack: center;
  justify-content: center;
  margin-top: 140px;
  display: -ms-flexbox;
  display: flex;
}

.menu__list {
  -ms-flex-direction: column;
  flex-direction: column;
  gap: 15px;
  width: max-content;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

.menu__link {
  color: #f8f8f8;
  position: relative;
}

.menu__link:after {
  content: "";
  background-color: #f8f8f8;
  width: 0;
  height: 1px;
  margin-top: 6px;
  transition: width .3s;
  display: block;
}

.menu__link:hover:after {
  width: 100%;
}

.menu__counter {
  color: #f8f8f8;
  text-align: center;
  background-color: #eb5757;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  font-family: Bebas Neue, sans-serif;
  font-size: 10px;
  line-height: 14px;
  position: absolute;
  right: -15px;
}

.menu__close {
  color: #f8f8f8;
  writing-mode: vertical-lr;
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  bottom: 150px;
  right: 5%;
  transform: rotate(180deg);
}

